
var canvas = document.getElementById('canvas')
var ctx = canvas.getContext('2d')
ctx.lineCap = "square"
console.log(ctx.lineCap)
var lineWidth = 8

autoSetCanvasSize(canvas)
listenToMouse(canvas)

var eraserEnable = false   //Ê©°ÁöÆÊì¶ÁîªÁ¨îÂàáÊç¢
eraser.onclick = function () {
    eraserEnable = true
    eraser.classList.add('active')
    brush.classList.remove('active')
}
brush.onclick = function () {
    eraserEnable = false
    brush.classList.add('active')
    eraser.classList.remove('active')
}

black.onclick = function () {
    ctx.fillStyle = 'black'
    ctx.strokeStyle = 'black'
    black.classList.add('active')
    red.classList.remove('active')
    yellow.classList.remove('active')
    green.classList.remove('active')
    blue.classList.remove('active')

}
red.onclick = function () {
    ctx.fillStyle = 'red'
    ctx.strokeStyle = 'red'
    black.classList.remove('active')
    red.classList.add('active')
    yellow.classList.remove('active')
    green.classList.remove('active')
    blue.classList.remove('active')
}
yellow.onclick = function () {
    ctx.fillStyle = 'yellow'
    ctx.strokeStyle = 'yellow'
    black.classList.remove('active')
    red.classList.remove('active')
    yellow.classList.add('active')
    green.classList.remove('active')
    blue.classList.remove('active')
}
green.onclick = function () {
    ctx.fillStyle = 'green'
    ctx.strokeStyle = 'green'
    black.classList.remove('active')
    red.classList.remove('active')
    yellow.classList.remove('active')
    green.classList.add('active')
    blue.classList.remove('active')
}
blue.onclick = function () {
    ctx.fillStyle = 'blue'
    ctx.strokeStyle = 'blue'
    black.classList.remove('active')
    red.classList.remove('active')
    yellow.classList.remove('active')
    green.classList.remove('active')
    blue.classList.add('active')
}
clear.onclick = function () {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
}

big.onclick = function () {
    lineWidth = 8
    big.classList.add('active')
    middle.classList.remove('active')
    small.classList.remove('active')
}
middle.onclick = function () {
    lineWidth = 5
    big.classList.remove('active')
    middle.classList.add('active')
    small.classList.remove('active')
}
small.onclick = function () {
    lineWidth = 2
    big.classList.remove('active')
    middle.classList.remove('active')
    small.classList.add('active')
}

download.onclick = function () {
    var url = canvas.toDataURL("image/png")
    console.log(url)
    var a = document.createElement('a')
    document.body.appendChild(a)
    a.href = url
    a.download = 'canvasÁîªÊùø.png'
    a.target = '_blank'
    a.click()
}
function listenToMouse(canvas) {
    var usingMouse = false //Èº†Ê†áËß¶ÂèëÊó∂Èó¥ÈªòËÆ§ËÆæÁΩÆfalse
    var lastPoint = { 'x': undefined, 'y': undefined } //Èº†Ê†áÊúÄÂêéÁÇπÂáªÁöÑÁÇπÂùêÊ†áÂàùÂßãÂåñ
    ctx.lineCap = "round"

    if (document.body.ontouchstart !== undefined) {  //Âà§Êñ≠ËÆæÂ§áÊòØÂê¶ÊîØÊåÅtouch‰∫ã‰ª∂

        canvas.ontouchstart = function (a) { //touchÂºÄÂßã
            console.log(a)
            var x = a.touches[0].clientX
            var y = a.touches[0].clientY
            usingMouse = true
            if (eraserEnable) {
                ctx.clearRect(x - 25, y - 25, 50, 50);
            }
            else {
                lastPoint = { 'x': x, 'y': y }
            }
        }
        canvas.ontouchmove = function (a) {  //touchÁßªÂä®
            var x = a.touches[0].clientX
            var y = a.touches[0].clientY
            if (!usingMouse) { return }

            if (eraserEnable) {
                ctx.clearRect(x - 10, y - 10, 20, 20);
            }
            else {
                var newPoint = { 'x': x, 'y': y }
                drawLine(lastPoint.x, lastPoint.y, newPoint.x, newPoint.y)
                lastPoint = newPoint
            }
        }
        canvas.ontouchend = function () {  //touchÁªìÊùü
            usingMouse = false
        }
    }
    else {                                 //Â¶ÇÊûú‰∏çÊîØÊåÅtouch‰∫ã‰ª∂ÔºåÂàôÊâßË°åÈº†Ê†á‰∫ã‰ª∂
        canvas.onmousedown = function (a) { //Èº†Ê†áÊåâ‰∏ã‰∫ã‰ª∂
            var x = a.clientX
            var y = a.clientY
            usingMouse = true
            if (eraserEnable) {
                ctx.clearRect(x - 10, y - 10, 20, 20);
            }
            else {
                lastPoint = { 'x': x, 'y': y }
                // drawPoint(x,y,1)
            }
        }
        canvas.onmousemove = function (a) {  //Èº†Ê†áÁßªÂä®‰∫ã‰ª∂
            var x = a.clientX
            var y = a.clientY
            if (!usingMouse) { return }

            if (eraserEnable) {
                ctx.clearRect(x - 10, y - 10, 20, 20);
            }
            else {
                var newPoint = { 'x': x, 'y': y }
                // drawPoint(x,y,1)
                drawLine(lastPoint.x, lastPoint.y, newPoint.x, newPoint.y)
                lastPoint = newPoint
            }
        }
        canvas.onmouseup = function () {  //Èº†Ê†áÊùæÂºÄ‰∫ã‰ª∂
            usingMouse = false
        }
    }
}

function drawPoint(x, y, radius) {
    ctx.beginPath()
    ctx.arc(x, y, radius, 0, Math.PI * 2)
    ctx.fill()
}

function drawLine(x1, y1, x2, y2) {
    ctx.beginPath()
    ctx.moveTo(x1, y1)
    ctx.lineWidth = lineWidth
    ctx.lineTo(x2, y2)
    ctx.stroke()
    ctx.closePath()
}

function autoSetCanvasSize(canvas) { //ÂÖ®Â±èÂáΩÊï∞
    resize()
    window.onresize = function () {
        resize()
    }
    function resize() {
        var pageWidth = document.documentElement.clientWidth
        var pageHeight = document.documentElement.clientHeight
        canvas.width = pageWidth
        canvas.height = pageHeight
    }
}